import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import AOS from 'aos';
import 'aos/dist/aos.css';
import { useSelector } from 'react-redux'
const Nav = ({ darkMode, setDarkMode }) => {
  useEffect(() => {
    AOS.init()
  }, [])
  const wishList = useSelector((state) => state.wishList)
  const [mobileMenuIsActive, setMobileMenuIsActive] = useState(false);
  const [color, setColor] = useState(false);
  const buttonStyles = "border-mainColor border-2 px-7 py-3 text-mainColor font-bold text-sm rounded-md"
  const token = localStorage.getItem("token")

  const handleOpenMobileMenu = () => {
    setMobileMenuIsActive((current) => !current);
  };

  const handleColor = () => {
    if (window.scrollY >= 90) {
      setColor(true);
    } else {
      setColor(false);
    }
  };

  window.addEventListener("scroll", handleColor);


  const handleLogOut = () => {
    localStorage.removeItem("token")
    window.location.reload()
  };

  return (
    <React.Fragment>
      <div
        className={
          color
            ? "bar change-bar bg-white dark:bg-slate-800 text-slate-800 dark:text-slate-100"
            : "bar bg-transparent text-black dark:text-white"
        }
      >
        <div className="desktop-nav w-100% hidden md:flex items-center justify-between p-8" data-aos="fade-down">
          <Link
            to="/"
            className="text-xl flex items-center text-inherit font-bold"
          >
            {darkMode === "light" ? (
              <img
                width="200px"
                src={"https://i.postimg.cc/8zDVjwR5/Logo-dark.png"}
                alt="logo"
              />) : (<img
                width="200px"
                src={"https://i.postimg.cc/yW4vpQ1S/Logo.png"}
                alt="logo"
              />)}
          </Link>
          <nav>
            <ul>
              <li className="inline-block mx-4 text-inherit relative hover:text-mainColor">
                <a href="/">
                  Home
                </a>
              </li>
              <li className="inline-block mx-4 text-inherit relative hover:text-mainColor">
                <a href="/contact">
                  Contact us
                </a>
              </li>
              <li className="inline-block mx-4 text-inherit relative hover:text-mainColor">
                <a href="/install">
                  installation
                </a>
              </li>
              {token && (
                <li className="inline-block mx-4 text-inherit relative hover:text-mainColor">
                  <a href="/profile">
                    profile
                  </a>
                </li>
              )}
            </ul>
          </nav>
          <div className="flex items-center gap-x-5">
            {token && (
              <a href="/WishList" className="relative flex items-center gap-x-2">
                <i className="fa-solid fa-face-grin-hearts text-red-500 text-xl"></i>
                <small>{wishList.wishListItems.length}</small>
              </a>
            )}
            <button onClick={() => {
              localStorage.setItem("theme", darkMode === "light" ? "dark" : "light")
              setDarkMode(darkMode === "light" ? "dark" : "light")
            }} >
              {darkMode === "light" ? <><i class="fa-solid fa-sun mr-3"></i> Light mode</> : <><i class="fa-solid fa-moon mr-3"></i> Dark mode</>}
            </button>
            {token ? <button onClick={handleLogOut} className={buttonStyles}>Sign out</button> :
              <React.Fragment>
                <a className={buttonStyles} href="/login">Login</a>
                <a className={buttonStyles} href="/signin">signin</a>
              </React.Fragment>}
          </div>
        </div>
      </div>
      <div
        className={
          color
            ? "bar change-bar bg-white dark:bg-slate-800 text-slate-800 dark:text-slate-100"
            : "bar bg-transparent text-black dark:text-white"
        }
      >
        <div className="mobile-nav w-100% p-6 bg-transparent flex md:hidden items-center justify-between" data-aos="fade-down">
          <h2 className="text-xl flex items-center text-inherit font-bold">
            {darkMode === "light" ? (
              <img
                width="200px"
                src={"https://i.postimg.cc/8zDVjwR5/Logo-dark.png"}
                alt="logo"
              />) : (<img
                width="200px"
                src={"https://i.postimg.cc/yW4vpQ1S/Logo.png"}
                alt="logo"
              />)}
          </h2>
          <div className="flex items-center gap-x-5">
            {token && (
              <a href="/WishList" className="relative flex items-center gap-x-2">
                <i className="fa-solid fa-face-grin-hearts text-red-500 text-xl"></i>
                <small>{wishList.wishListItems.length}</small>
              </a>
            )}
            <button className={darkMode === "light" ? `text-white ${color && "text-inherit"}` : `text-black ${color && "text-inherit"}`} onClick={handleOpenMobileMenu}>
              <ion-icon name="grid-sharp"></ion-icon>
            </button>
          </div>
        </div>
      </div>
      <div className="p-6"></div>
      <div
        className={
          mobileMenuIsActive
            ? " md:hidden menu top-20 z-[1000]"
            : "-top-480px menu"
        }
      >
        <nav>
          <ul>
            <li className="bg-slate-300 dark:bg-slate-700 p-3 border-l-4 border-indigo-500">
              <div className="flex items-center">
                <a className="ml-2 font-bold text-black dark:text-white" href="/">
                  Home
                </a>
              </div>
            </li>
            <li className="hover:bg-slate-300 dark:hover:bg-slate-700 p-3 hover:border-l-4 hover:border-slate-300 my-2 transition duration-300">
              <div className="flex items-center">
                <a className="ml-2 font-bold text-black dark:text-white" href="/contact">
                  Contact us
                </a>
              </div>
            </li>
            <li className="hover:bg-slate-300 dark:hover:bg-slate-700 p-3 hover:border-l-4 hover:border-slate-300 my-2 transition duration-300">
              <div className="flex items-center">
                <a className="ml-2 font-bold text-black dark:text-white" href="/install">
                  installation
                </a>
              </div>
            </li>
            {token && (
              <li className="hover:bg-slate-300 dark:hover:bg-slate-700 p-3 hover:border-l-4 hover:border-slate-300 my-2 transition duration-300">
                <div className="flex items-center">
                  <a className="ml-2 font-bold text-black dark:text-white" href="/profile">
                    profile
                  </a>
                </div>
              </li>
            )}
            <div className="flex flex-col items-start gap-5 p-3">
              <button onClick={() => {
                localStorage.setItem("theme", darkMode === "light" ? "dark" : "light")
                setDarkMode(darkMode === "light" ? "dark" : "light")
              }}>
                {darkMode === "light" ? <div className="text-white"><i class="fa-solid fa-sun mr-3"></i> Light mode</div> : <><i class="fa-solid fa-moon mr-3"></i> Dark mode</>}
              </button>
              {token ? <button onClick={handleLogOut} className={buttonStyles}>Sign out</button> :
                <React.Fragment>
                  <a className={buttonStyles} href="/login">Login</a>
                  <a className={buttonStyles} href="/signin">signin</a>
                </React.Fragment>}
            </div>
          </ul>
        </nav>
      </div>
    </React.Fragment>
  );
};

export default Nav;
